<template>
  <div class="Enemies">
    <div
      v-for="enemy in enemies"
      :key="enemy.id"
      :style="styles[enemy.id]"
      class="enemy"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex';

// С запасом, чтобы объект полностью (если повезёт) скрылся из виду.
const magicSize = 100;

export default {
  name: 'Enemies',

  data() {
    return {
      styles: {},
    };
  },

  computed: {
    ...mapState('enemies', ['enemies']),
  },

  mounted() {
    this.setStyles();
    this.runLoop();
  },

  methods: {
    setStyles() {
      this.enemies.forEach(({ id }) => {
        this.$set(this.styles, id, {});
      });
    },

    runLoop() {
      requestAnimationFrame(this.runLoop);
      this.animate();
    },

    animate() {
      // Осторожно! Мутации проникают в хранилище, которое слушает компонент Player.
      this.enemies.forEach(({ id, position, scale }) => {
        this.scaling(scale);

        this.movement(position);
        this.screening(position);

        this.update({ id, position, scale });
      });
    },

    scaling(scale) {
      if (scale.value > scale[scale.type]) {
        scale.type = 'min';
        scale.value -= scale.step;
      } else {
        scale.type = 'max';
        scale.value += scale.step;
      }
    },

    movement(position) {
      position.top.value += position.top.step;
    },

    // Сброс положения до начального при достижении края экрана.
    screening(position) {
      if (position.top.value - magicSize >= window.innerHeight) {
        position.top.value = position.top.default;
      }
    },

    update({ id, position: { left, top }, scale }) {
      this.styles[id] = {
        transform: `translate3d(${left.value}px, ${top.value}px, 0) scale(${scale.value})`,
      };
    },
  },
};
</script>

<style scoped>
.Enemies {
  position: absolute;
  width: 100vw;
  height: 100vh;
}

/* Для выноса рассчётов на gpu используется translate3d. */
/* will-change: transform; - Размывает края при масштабировании. */
.enemy {
  position: absolute;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  background-color: brown;
}
</style>
