<template>
  <div class="Players">
    <div
      v-for="player in players"
      :key="player.tick"
      :style="getStyle(player)"
      class="player"
    >
      <div class="health">
        <div>Враг</div>
        <div>{{ player.health }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Players',

  data() {
    return {
      players: {},
    };
  },

  mounted() {
    window.app.connection.on('clients', (clients) => {
      delete clients[window.app.clientId];
      this.players = clients;
    });
  },

  methods: {
    // При первой заходе сервер не высылает позиций других игроков, потому что там их еще нет.
    getStyle(player) {
      const { health, position, scale, isCrossed } = player;
      const color = this.getHealthColor(health);

      let style;

      if (position) {
        style = {
          transform: `translate(${position.left}px, ${position.top}px) scale(${scale})`,
          backgroundColor: isCrossed ? 'darkred' : color,
        };
      } else {
        style = {
          transform: `translate(0, 0) scale(1)`,
          backgroundColor: isCrossed ? 'darkred' : color,
        };
      }

      return style;
    },

    // todo так себе решение, вписать значение по умолчанию здесь - но не важно!
    getHealthColor(health = 200) {
      let color = '';

      switch (true) {
        case health > 150:
          color = 'hsl(120, 80%, 40%)';
          break;

        case health > 100:
          color = 'hsl(120, 80%, 30%)';
          break;

        case health > 50:
          color = 'hsl(120, 80%, 20%)';
          break;

        case health > 0:
          color = 'hsl(120, 80%, 10%)';
          break;

        default:
          color = 'black';
      }

      return color;
    },
  },
};
</script>

<style scoped>
.Players {
  position: absolute;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

.player {
  position: absolute;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  transition: background-color 0.1s;
  will-change: transform;
}

.health {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: bold;
  color: white;
  will-change: contents;
}
</style>
